	module.exports = {"data":{"wordpressPage":{"title":"Recommended misoprostol regimen for treatment of incomplete and missed abortion for 13 weeks or larger uterine size","content":"<h2>Recommendation:</h2>\n<ul>\n<li>Misoprostol in a dose of at least 200mcg buccally, sublingually or vaginally every six hours until expulsion.</li>\n<li>Pretreatment with mifepristone 200mg orally 1-2 days before misoprostol may decrease the time from induction to fetal expulsion.</li>\n<li>The misoprostol-only or mifepristone-misoprostol regimens for induced abortion at or after 13 weeks gestation may also be used.</li>\n<li>Where skilled providers and supportive facilities exist, dilatation and evacuation (D&amp;E) may be offered.</li>\n</ul>\n<h3>Strength of recommendation: Strong</h3>\n<h3>Quality of evidence: Low</h3>\n<h3>Background</h3>\n<p>The majority of postabortion care research and programs focus on women with uterine size less than 13 weeks (Ipas, 2013). However, where unsafe abortion is prevalent, as many as 40% of women needing postabortion care present at or after 13 weeks gestation (Ministry of Health of Kenya, Ipas, &amp; Guttmacher Institute, 2013). Women may present with incomplete abortion, retained placenta, fetal demise or ruptured membranes, all of which require uterine evacuation.</p>\n<h3>Medical regimens</h3>\n<p>Evidence is limited to suggest the optimal medical regimen for postabortion care at or after 13 weeks uterine size, but a systematic review of the literature suggests that at least 200mcg vaginally, sublingually or buccally given every six hours is effective (Bracken et al., 2014; Mark, Borgatta, &amp; Edelman, 2015). Pretreatment with mifepristone 1-2 days prior to misoprostol may reduce the time to fetal expulsion (Niinimaki et al., 2017; Stibbe &amp; de Weerd, 2012). Guidelines by the International Federation of Gynecology and Obstetrics (FIGO) based on available evidence and expert opinion regarding the use of misoprostol alone in gynecology and obstetrics supports using medical abortion regimens at or after 13 weeks (Morris et al., 2017).</p>\n<h3>D&amp;E</h3>\n<p>No studies have compared medical management versus vacuum aspiration or D&amp;E for postabortion care at or after 13 weeks. D&amp;E can be offered to women for postabortion care where skilled providers and supportive facilities exist (WHO, 2014).</p>\n<h3>References</h3>\n<p>Bracken, H., Ngoc, N. T., Banks, E., Blumenthal, P. D., Derman, R. J., Patel, A., . . . Winikoff, B. (2014). Buccal misoprostol for treatment of fetal death at 14-28 weeks of pregnancy: A double-blind randomized controlled trial. <em>Contraception, 89</em>(3), 187-192. DOI:10.1016/j.contraception.2013.11.014</p>\n<p>Ipas. (2013). <em>Woman-centered postabortion care: Reference manual</em> (2nd ed.). K.L. Turner &amp; A.B. Huber (Eds.). Chapel Hill, NC: Ipas.</p>\n<p>Mark, A., Borgatta, L., &amp; Edelman, A. (2015). Second trimester postabortion care for ruptured membranes, fetal demise, and incomplete abortion. <em>International Journal of Obstetrics &amp; Gynaecology</em>, <em>129</em>(2), 98-103.</p>\n<p>Ministry of Health of Kenya, Ipas, and Guttmacher Institute. (2013). <em>Incidence and complications of unsafe abortion In Kenya: Key findings of a national study. </em>African Population and Health Research Center: Nairobi.</p>\n<p>Morris, J. L., Winikoff, B., Dabash, R., Weeks, A., Faundes, A., Gemzell-Danielsson, K., … Visser, G. H. A. (2017). FIGO’s updated recommendations for misoprostol used alone in gynecology and obstetrics. <em>International Journal of Gynecology &amp; Obstetrics, 138, </em>363-366.</p>\n<p>Niinimaki, M., Mentula, M., Jahangiri, R., Mannisto, J., Haverinen, A., &amp; Heikinheimo, O. (2017). Medical treatment of second-trimester fetal miscarriage: A retrospective analysis. <em>PLoS ONE, 12</em>(7), e0182198.</p>\n<p>Stibbe, K.J. &amp; de Weerd, S. (2012). Induction of delivery by mifepristone and misoprostol in termination of pregnancy and intrauterine fetal death: 2nd and 3rd trimester induction of labour. <em>Archives of Gynecology and Obstetrics</em>, <em>286</em>(3), 795-796.</p>\n<p>World Health Organization. (2014). <em>Clinical practice handbook for safe abortion. </em>Geneva: World Health Organization Press.</p>\n","fields":{"path":"postabortion-care/miso-regimen-for-tx-of-incomplete-and-missed-abortion-for-at-or-after-13"},"acf":{"last_reviewed":"20171213","landing_page":false}},"parent":{"title":"Postabortion care","fields":{"path":"postabortion-care"},"wordpress_parent":159},"grandparent":null,"site":{"id":"Site","siteMetadata":{"title":"Ipas Clinical Updates"}}},"pathContext":{"id":"bc473e73-d4ac-560c-b2b5-5a323e8bed74","title":"Recommended misoprostol regimen for treatment of incomplete and missed abortion for 13 weeks or larger uterine size","content":"<h2>Recommendation:</h2>\n<ul>\n<li>Misoprostol in a dose of at least 200mcg buccally, sublingually or vaginally every six hours until expulsion.</li>\n<li>Pretreatment with mifepristone 200mg orally 1-2 days before misoprostol may decrease the time from induction to fetal expulsion.</li>\n<li>The misoprostol-only or mifepristone-misoprostol regimens for induced abortion at or after 13 weeks gestation may also be used.</li>\n<li>Where skilled providers and supportive facilities exist, dilatation and evacuation (D&amp;E) may be offered.</li>\n</ul>\n<h3>Strength of recommendation: Strong</h3>\n<h3>Quality of evidence: Low</h3>\n<h3>Background</h3>\n<p>The majority of postabortion care research and programs focus on women with uterine size less than 13 weeks (Ipas, 2013). However, where unsafe abortion is prevalent, as many as 40% of women needing postabortion care present at or after 13 weeks gestation (Ministry of Health of Kenya, Ipas, &amp; Guttmacher Institute, 2013). Women may present with incomplete abortion, retained placenta, fetal demise or ruptured membranes, all of which require uterine evacuation.</p>\n<h3>Medical regimens</h3>\n<p>Evidence is limited to suggest the optimal medical regimen for postabortion care at or after 13 weeks uterine size, but a systematic review of the literature suggests that at least 200mcg vaginally, sublingually or buccally given every six hours is effective (Bracken et al., 2014; Mark, Borgatta, &amp; Edelman, 2015). Pretreatment with mifepristone 1-2 days prior to misoprostol may reduce the time to fetal expulsion (Niinimaki et al., 2017; Stibbe &amp; de Weerd, 2012). Guidelines by the International Federation of Gynecology and Obstetrics (FIGO) based on available evidence and expert opinion regarding the use of misoprostol alone in gynecology and obstetrics supports using medical abortion regimens at or after 13 weeks (Morris et al., 2017).</p>\n<h3>D&amp;E</h3>\n<p>No studies have compared medical management versus vacuum aspiration or D&amp;E for postabortion care at or after 13 weeks. D&amp;E can be offered to women for postabortion care where skilled providers and supportive facilities exist (WHO, 2014).</p>\n<h3>References</h3>\n<p>Bracken, H., Ngoc, N. T., Banks, E., Blumenthal, P. D., Derman, R. J., Patel, A., . . . Winikoff, B. (2014). Buccal misoprostol for treatment of fetal death at 14-28 weeks of pregnancy: A double-blind randomized controlled trial. <em>Contraception, 89</em>(3), 187-192. DOI:10.1016/j.contraception.2013.11.014</p>\n<p>Ipas. (2013). <em>Woman-centered postabortion care: Reference manual</em> (2nd ed.). K.L. Turner &amp; A.B. Huber (Eds.). Chapel Hill, NC: Ipas.</p>\n<p>Mark, A., Borgatta, L., &amp; Edelman, A. (2015). Second trimester postabortion care for ruptured membranes, fetal demise, and incomplete abortion. <em>International Journal of Obstetrics &amp; Gynaecology</em>, <em>129</em>(2), 98-103.</p>\n<p>Ministry of Health of Kenya, Ipas, and Guttmacher Institute. (2013). <em>Incidence and complications of unsafe abortion In Kenya: Key findings of a national study. </em>African Population and Health Research Center: Nairobi.</p>\n<p>Morris, J. L., Winikoff, B., Dabash, R., Weeks, A., Faundes, A., Gemzell-Danielsson, K., … Visser, G. H. A. (2017). FIGO’s updated recommendations for misoprostol used alone in gynecology and obstetrics. <em>International Journal of Gynecology &amp; Obstetrics, 138, </em>363-366.</p>\n<p>Niinimaki, M., Mentula, M., Jahangiri, R., Mannisto, J., Haverinen, A., &amp; Heikinheimo, O. (2017). Medical treatment of second-trimester fetal miscarriage: A retrospective analysis. <em>PLoS ONE, 12</em>(7), e0182198.</p>\n<p>Stibbe, K.J. &amp; de Weerd, S. (2012). Induction of delivery by mifepristone and misoprostol in termination of pregnancy and intrauterine fetal death: 2nd and 3rd trimester induction of labour. <em>Archives of Gynecology and Obstetrics</em>, <em>286</em>(3), 795-796.</p>\n<p>World Health Organization. (2014). <em>Clinical practice handbook for safe abortion. </em>Geneva: World Health Organization Press.</p>\n","last_reviewed":"20171213","parent":166,"grandparent":null}}

